@font-face {
  font-family: "PT Sans";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
@font-face {
  font-family: "PT Sans";
  font-weight: bold;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
body {
  margin: 0;
  max-width: 1200px;
  min-width: 320px;
}
@media (min-width: 1200px) {
  body {
    margin: 0 auto;
  }
}
img {
  max-width: 100%;
  object-fit: cover;
  z-index: 1;
}
a {
  text-decoration: none;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.header {
  position: sticky;
  top: 0;
  z-index: 100;
  width: 100%;
  min-height: 56px;
}
.header__logo {
  position: absolute;
  z-index: 100;
  top: 0;
  left: calc(50% - 92px);
  width: 185px;
  height: 96px;
  margin: 0;
}
.header-nav {
  position: relative;
  min-width: 320px;
  min-height: 56px;
  background-color: #ffffff;
}
.header-nav__toggle {
  display: block;
  position: absolute;
  z-index: 300;
  top: 17px;
  right: 17px;
  width: 23px;
  height: 23px;
