  background-repeat: no-repeat;
  background-color: transparent;
}
.header-nav__toggle--burger {
  background-image: url("data:image/svg+xml,%3Csvg width='23' height='22' viewBox='0 0 23 22' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M22.9 0H0V4H22.9V0Z' fill='%2381B3D2'/%3E%3Cpath d='M22.9 9H0V13H22.9V9Z' fill='%2381B3D2'/%3E%3Cpath d='M22.9 18H0V22H22.9V18Z' fill='%2381B3D2'/%3E%3C/svg%3E%0A");
}
.header-nav__wrapper {
  display: flex;
  flex-direction: column;
  z-index: 200;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
.header-nav__wrapper--closed {
  display: none;
}
.header-nav__list {
  list-style-type: none;
  text-align: center;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background-color: #81b3d2;
}
.header-nav__list::after {
  content: "";
  position: absolute;
  bottom: 0;
  z-index: 100;
  width: 100%;
  height: 7px;
  mix-blend-mode: normal;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
}
.header-nav__item a {
  text-decoration: none;
  color: #ffffff;
  width: 100%;
  padding: 0 10px;
}
.header-nav__item {
  position: relative;
  display: flex;
  width: auto;
  min-height: 55px;
  mix-blend-mode: normal;
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
  align-items: center;
  justify-content: center;
  margin: 0.5px;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
  text-align: center;
}
@media (min-width: 768px) {
  .header-nav {
    display: block;
    min-width: 640px;
  }
  .header-nav__toggle {
    display: none;
  }
  .header-nav__toggle--burger {
    display: none;
  }
  .header-nav__wrapper {
    flex-direction: row;
    justify-content: space-between;
    z-index: 50;
  }
  .header-nav__wrapper--closed {
    display: flex;
  }
  .header-nav__list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    justify-content: space-between;
    width: 100%;
    background-color: #ffffff;
  }
  .header-nav__list::after {
    display: none;
  }
  .header-nav__item {
    min-width: 153px;
    box-shadow: none;
    font-size: 14px;
  }
  .header-nav__item:nth-child(4n+3) {
    grid-column: 4/5;
  }
  .header-nav__item a {
    color: #000000;
  }
}
@media (min-width: 1200px) {
  max-width: 1200px;
  margin: 0 auto;
}
