.hero__image {
  display: block;
  margin-top: 60px;
}
.hero__image--index {
  margin-top: 90px;
}
@media (min-width: 768px) {
  .hero {
    min-height: 302px;
    background-image: url("data:image/svg+xml,%3Csvg width='1200' height='57' viewBox='0 0 1200 57' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M249.6 23.7L0 0V57H249.6H600L249.6 0V23.7Z' fill='white'/%3E%3Cpath d='M950.4 23.7V0L600 57H950.4H1199.9V0L950.4 23.7Z' fill='white'/%3E%3C/svg%3E%0A"), url("../img/back-desktop.jpg");
