}
.page {
  height: 100%;
  font-size: 14px;
  font-family: "PT Sans", "Arial", sans-serif;
  line-height: 21px;
}
.page__body {
  min-width: 320px;
  min-height: 100%;
  margin: 0;
}
@media (min-width: 1200px) {
  .page__body {
    max-width: 1200px;
    margin: 0 auto;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 1;
  background-color: transparent;
  cursor: pointer;
}
.advantage__list {
  list-style-type: none;
  text-align: center;
  margin: 0;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  background-color: #ffffff;
  padding: 23px 18px 52px 18px;
}
@media (min-width: 768px) {
  .advantage__list {
    grid-column-start: 1;
    grid-column-end: 4;
    flex-direction: row;
    justify-content: center;
    width: 100%;
    min-height: 330px;
  }
}
.advantage__item {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  padding-left: 47px;
  padding-right: 25px;
  padding-top: 30px;
  background-color: #ffffff;
}
